<template>
    <div class="puy">
        <i-top :title ='title' :flag ='flag'></i-top>
        <!-- 收货地址 -->
        <p class="dz" ref ='topPuy'>
            <span class ='icon'></span>
            <span>上海</span>
            <span>陆家嘴</span>
            <span>176****1</span>
            <span>张先生</span>
        </p>
        <!-- 结算列表 -->
        <div class="shopList" ref='bt'>
            <ul>
                <li v-for ='item in shopList' :key ='item.name'>
                    <div class="left">
                        <img v-lazy="item.ImgUrl">
                    </div>
                    <div class="right">
                        <p class="Ritop">{{item.name}}</p>
                        <p class="js">
                            <span>{{item.colorName}}</span>
                            <span>{{item.sizeName}}</span>
                        </p>
                        <p class="info">
                            <span class ='price'>￥{{item.price}}</span>
                            <span class ='page'>x{{item.page}} </span>
                        </p>
                    </div>
                </li>
            </ul>
        </div>
        <!-- 支付-->
        <div class="bt">
            <p>
                <span>商品金额</span>
                <span class="BtRight">￥94.00</span>
            </p>
            <p>
                <span>运费</span>
                <span class="BtRight">+￥0.00</span>
            </p>
            <div class="zhifu">
                <span>在线支付</span>
            </div>
        </div>
        <!-- 底部LOGO -->
        <div class="logo">
            <img src="data:image/png;base64,">
        </div>
    </div>
</template>

<script>
import iTop from '../assets/vue/top'
export default {
    data(){
        return {
            title :"确认订单",
            flag : false,
            shopList : []
        } 
    },
    created(){
        document.title = '确认订单'
      this.shopList = this.$route.query.data
    },
    mounted(){
        window.addEventListener('scroll',this.scrollTop)
    },
    methods:{
        scrollTop(){
           if(this.$refs.topPuy){
                if(window.pageYOffset >= 30){
                this.$refs.topPuy.style.position ='fixed'
                this.$refs.topPuy.style.top =0
                this.$refs.topPuy.style.width ='100%'
                this.$refs.bt.style.marginTop = '100px'
            }else {
                this.$refs.topPuy.style.position ='static'
                this.$refs.bt.style.marginTop = '0'
            }
           }
        }
    },
    components:{iTop}
}
</script>

<style lang="less" scoped>
.logo{
    text-align: center;
    margin-top: 80px;
    img{
        width: 200px;

    }
}
.puy{
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: #f7f7f7;
    .dz{
        height: 100px;
        line-height: 100px;
        background:url('data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAAIAAAAAKBAMAAACOO0tGAAAABGdBTUEAALGPC/xhBQAAAAFzUkdCAK7OHOkAAAAnUExURf///4u16Oxtbezz/J3B7NDh9vSmprjS8vGNjfjDw/vd3f7w8O57e0EOI68AAABSSURBVCjPY2CAAE4l7GACVJ4hUBArEIXJL8KuXw0mz4xdv2ABVJ77EHYDGmAGmGDXLwKT58CuX2cDTIEjdgOcYfJJ2A3Qgsmz4/CBwWgQUiMIAXzCOFELLk/nAAAAAElFTkSuQmCC') -7px bottom repeat-x;
        background-size: 100px 10px;
        background-color: #fff;
        span{
            margin : 0 30px;
        }
        .icon{
            display: inline-block;
            height: 40px;
            width: 40px;
            background-image:url('data:image/png;base64,');
            background-size: cover;
            margin-right: 10px;
        }
    }
    .bt{
        background-color: #fff;
        margin-top: 30px;
        padding : 0 20px;
        .zhifu{
            width: 90%;
            margin: 20px auto;
            height: 100px;
            line-height: 100px;
            color:#fff;
            text-align: center;
            background-color: #3884ff;
            border-radius: 50px;
        }
        p{
            display: flex;
            font-size: 35px;
            justify-content: space-between;
            line-height: 50px;
            align-items: center;
            .BtRight {
                color:red;
            } 
        }
    }
    .shopList{
        margin-top: 40px;
        padding : 0 20px;
        background-color: #fff;
        ul{
            li{
                display: flex;
                width: 100%;
                margin : 30px 0;
                .left{
                    img{
                        width:  170px;
                        height: 170px;
                        border-radius: 20px;
                    }
                    
                }
                .right{
                    margin-left: 30px;
                    width: 100%;
                    overflow: hidden;
                    .Ritop{
                        font-size: 35px;
                        font-weight: 700;
                        text-overflow: ellipsis;
                        overflow: hidden;
                        display: -webkit-box;
                        -webkit-line-clamp: 2;
                        -webkit-box-orient: vertical;
                    }
                    .js{
                        margin-top: 20px;
                        text-overflow: ellipsis;
                        overflow: hidden;
                        white-space: nowrap;
                        span{
                            padding-right:20px;
                            color:#999;
                        }
                    }
                    .info{
                        margin-top: 20px;
                        .price{
                            color:red;
                            font-size: 30px;
                        }
                        .page{
                            float: right;
                            color:#999;
                            font-size: 20px;
                        }
                    }
                }
            }
        }
    }
}
</style>